import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 0,
            children: [],
            search: '',
            user: this.$store.state.userInfo,
            ListDetails: this.$store.state.checkCkooseList,
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
            medSearchList: null,
            inputFlag: false,
			censorateList:[],
			drIdList:[],
			info:'',
			saveDr:null
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails.length){
                this.ListDetails.forEach(v => {
                    p += (v.ItemPrice * v.ItemNum)
                })
            }
            return p.toFixed(2)
        }
    },
    created(){
// 		const { saveDrCensorate,censorate } = this.$store.state
// 		this.saveDr = saveDrCensorate
// 		if(this.saveDr){
// 			this.pull(this.saveDr.CensorateId)
// 			this.info = this.saveDr
// 			this.censorateList = censorate
// 		}else{
// 			this.pullCensorate()
// 		}
		this.pullCensorate()
    },
	mounted(){
		
	},
	beforeDestroy(){
	    window.onscroll = null
	},
	beforeRouteLeave(to,from,next){   //离开
		window.onscroll = null
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance
		}
		next()
	},
	beforeRouteEnter(to, from, next) {  //进入前 
		let flag = false
		next(vm => {
			vm.getMore()
			if(!vm.$store.state.saveJCList){
				vm.act = 0
				vm.pullCensorate()
			}else{
				setTimeout(()=>{
					vm.act = vm.$store.state.saveJCList.act
					vm.$refs.el2.scrollTop = to.meta.scollTopPosition
				},1)
			}
		});
	},
    methods:{
        choose(id,i,idx){
            this.act = idx 
            this.secondList(id,i)
        },
		searchFor(){
			this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ //获取医生合作检查机构
			   DrId:this.user.Data.User.DoctorId,
			}).then(o => {
				let arr = {
					act:this.act,
					vTop:this.scrollTopDistance
				}
				this.$store.dispatch('pushsaveJCList',arr)
			    const censorateItemName = this.search
			    const censorateId = o.CensorateId
			    const censorateTypeId = 0
			    this.$store.dispatch('pushcId',censorateTypeId)
			    this.$store.dispatch('pushsearchText','')
			    this.$router.push(`/inspect/medicine-list?censorateItemName=${censorateItemName}&censorateId=${censorateId}&censorateTypeId=${censorateTypeId}`)
			})
		},
		listShow(){
			if(this.ListDetails.length){
				return this.$toast('一张检查单只能对应一个检查机构，您已经选择了当前机构开单，不能切换其他机构！')
			}
			if(this.saveDr){
				if(this.ListDetails.length){
					if(this.info.CensorateId == this.saveDr.CensorateId){
						return this.$toast('一张检查单只能对应一个检查机构，您已经选择了当前机构开单，不能切换其他机构！')
					}
				}
			}
			const cBg = this.$refs.cBg
			const cList = this.$refs.cList
			cList.classList.add('animateleft')
			cList.classList.remove("animateright");
			cBg.style.display = "block"
		},
		listHide(){
			const cBg = this.$refs.cBg
			const cList = this.$refs.cList
			cList.classList.add('animateright')
			cList.classList.remove("animateleft");
			cBg.style.display = "none"
		},
		pullCensorate(){
			this.$get('InspectionAPI/Censorate/QueryDefaultCensorate',{ //获取医生合作检查机构
			   DrId: this.user.Data.User.DoctorId,
			}).then(o => {
			    this.drIdList = o
				if(this.drIdList){
					this.info = this.drIdList
				}else{
					this.info = this.censorateList[0]
				}
				this.pull(this.info.CensorateId)
			})
		},
        pull(id){
            this.$get('InspectionAPI/Censorate/QueryCensorateTypeByTypeId',{
               CensorateId: id,
               CensorateTypeId: 0
            }).then(o => {
                this.items = o
				if(o.length != 0){
					this.secondList(id,o[0].CensorateTypeId)
				}else{
					this.children = []
				}
				this.$store.dispatch('pushsaveDrCensorate',o[0])
            })
        },
		secondList(id,CensorateTypeId){
			this.$get('InspectionAPI/Censorate/QueryCensorateTypeByTypeId',{
			   CensorateId: id,
			   CensorateTypeId: CensorateTypeId
			}).then(o => {
			    this.children = o
			})
		},
		goList(i){
			let arr = {
				act:this.act,
				vTop:this.scrollTopDistance
			}
			this.$store.dispatch('pushsaveJCList',arr)
			const censorateItemName = ""
			const censorateId = i.CensorateId
			const censorateTypeId = i.CensorateTypeId
			this.$store.dispatch('pushcId',censorateTypeId)
			this.$store.dispatch('pushsearchText','')
			this.$router.push(`/inspect/medicine-list?censorateItemName=${censorateItemName}&censorateId=${censorateId}&censorateTypeId=${censorateTypeId}`)
		},
		chooseCensorate(i){
			this.pull(i.CensorateId)
			this.listHide()
			this.info = i
			this.act = 0
			this.$store.dispatch('pushsaveDrCensorate',i)
		},
		getMore(){
		    const el2 = this.$refs.el2
		    let timer = null
		    el2.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
					 const top = el2.scrollTop
								|| el2.pageYOffset
								|| el2.scrollTop,
		                    h = el2.scrollHeight,
		                    wh = window.innerHeight
					this.scrollTopDistance = top;
		
		        }, 100)
		    }
		},

    }
}



